.banner{
    background-color: #ffd8bf96;
    height: 500px;
    margin-left: 50px;
    margin-right: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 20px;
}
.content{
    padding: 30px;
}
.banner:hover{
    filter: brightness(75%);
}
.banner{
    transition: filter 0.5s;
}